.container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}
.card {
    box-shadow: 0 13px 8px 0 rgba(0, 0, 0, 0.2);
    min-width: 300px;
    max-width: 300px;
    margin: auto;
    text-align: center;
    font-family: arial;
  }
  
  .price {
    color: grey;
    font-size: 22px;
  }
  
  .card button {
    border: none;
    outline: 0;
    padding: 12px;
    color: white;
    background-color: #000;
    text-align: center;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
  }
  
  .card button:hover {
    opacity: 0.7;
  }